<template>
    <div class="basic-info-container">
        <h2>基本資訊</h2>
        <div class="input-container">
            <input class="input-area" type="number" v-model="age" placeholder="年齡" />
            <input class="input-area" type="number" v-model="weight" placeholder="體重 (單位: 公斤)" />
            <input class="input-area" type="number" v-model="height" placeholder="身高 (單位: 公分)" />
        </div>

        <div class="select-container">
            <label for="genderSelect">選擇性別:</label>
            <select id="genderSelect" class="gender-select" v-model="gender">
                <option value="null">-- 請選擇 --</option>
                <option value="M">男</option>
                <option value="F">女</option>
            </select>
        </div>

        <div class="select-container">
            <label for="strengthSelect">運動強度:</label>
            <select id="strengthSelect" v-model="sportStrength">
                <option value="null">-- 請選擇 --</option>
                <option value="1.0">什麼都不幹</option>
                <option value="1.2">只進行正常的學習或工作, 沒運動</option>
                <option value="1.4">學習或工作外, 適量運動 (偶爾跑步走路)</option>
                <option value="1.6">不是從事體力相關職業, 但每天規律上健身房運動</option>
                <option value="1.8">從事體力相關職業 + (規律訓練 or 一天練兩次)</option>
                <option value="2.0">從事超繁重體力職業 + 高強度訓練</option>
            </select>
        </div>

        <h3>
            BMR 基礎代謝率 -
            <span v-if="BMR !== null">
                {{ BMR }} (大卡)
                <span class="hint">
                    BMR 指人體在休息狀態下，維持新陳代謝所需的熱量，例如：呼吸、器官運作、體溫維持等，即使整天躺著不動也會消耗的最低熱量。BMR 會隨著年紀增加或體重減輕而降低，會隨著肌肉量增加而上升。
                </span>
            </span>
            <span v-else>(上方數值未填妥)</span>
        </h3>

        <h3>
            TDEE 每日總消耗熱量 -
            <span v-if="TDEE !== null">
                {{ TDEE }} (大卡)
                <span class="hint">
                    TDEE 是身體一整天下來, 包括基礎代謝、活動量、吃東西所消耗的熱量。不同的生活型態需要的熱量也不一樣，當每天攝取的熱量和 TDEE 相等，便達到「熱量平衡」。
                </span>
            </span>
            <span v-else>(上方數值未填妥)</span>
        </h3>

        <div class="input-container">
            減少 <input class="input-area" type="number" v-model="percentage" placeholder="百分比" /> 百分比 (10% ~ 20%)
        </div>
        <h3>
            TDEE 減少百分比 -
            <span v-if="TDEEDeducted !== null">
                {{ TDEEDeducted }} (大卡) (每日需減少攝入 {{ TDEE * (+percentage / 100) }} 大卡)
            </span>
            <span v-else>(上方數值未填妥)</span>
        </h3>
    </div>
</template>

<script>
import { ref, computed } from '@vue/reactivity'

export default {
    setup() {
        const age = ref(null);
        const weight = ref(null);
        const height = ref(null);
        const gender = ref(null);
        const sportStrength = ref(null);
        const percentage = ref(null);

        const BMR = computed(() => {
            if (gender.value === null
                || weight.value === null
                || height.value === null
                || age.value === null) return null;

            const map = {
                M: {
                    default: 66,
                    weight: 13.7,
                    height: 5,
                    age: 6.8
                },
                F: {
                    default: 655,
                    weight: 9.6,
                    height: 1.7,
                    age: 4.7
                }
            }

            const odd = map[gender.value];

            return odd.default + (odd.weight * weight.value) + (odd.height * height.value) - (odd.age * age.value);
        })

        const TDEE = computed(() => {
            if (BMR.value === null || sportStrength.value === null) return null;

            return BMR.value * +sportStrength.value;
        })

        const TDEEDeducted = computed (() => {
            if (TDEE.value === null || percentage.value == null) return null;

            return TDEE.value - (TDEE.value * (+percentage.value / 100));
        })

        return {
            age,
            weight,
            height,
            gender,
            sportStrength,
            percentage,

            BMR,
            TDEE,
            TDEEDeducted
        }
    }
}
</script>

<style lang="css" scoped>
.input-container {
    width: 500px;
}

.select-container {
    width: 400px;
}

.hint {
    font-size: 0.75rem;
}
</style>